import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FileUploadComponent } from './file-upload/file-upload.component';
import { RejectionQueueComponent } from './rejection-queue/rejection-queue.component';
import { AuthorizeExcelComponent } from './authorize-excel/authorize-excel.component';
import { CanDeactivateGuard } from 'src/app/shared/guards/can-deactivate.guard';


const routes: Routes = [
  {
    path:"upload-excel",
    component:FileUploadComponent,
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path:"rejection_queue",
    component:RejectionQueueComponent
  },
  {
    path:"authorize_excel",
    component:AuthorizeExcelComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FileUploadRoutingModule { }
